#parent_results {
  margin: 0 auto;
  padding-bottom: 14em;
}
#parent_results #results {
  display: flex;
  flex-direction: column;
  max-width: 60em;
  margin: 0 auto;
}
#parent_results #results .format-heading {
  color: var(--headings);
  margin: 20px 0 10px 10px;
}
#parent_results #results div {
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-start;
}
#parent_results #results div a {
  text-decoration: none;
  margin: 5px 10px;
}
#parent_results #results div a .resultContainer {
  display: flex;
  flex-direction: column;
  width: 130px;
}
#parent_results #results div a .resultContainer img {
  width: 130px;
}
#parent_results #results div a .resultContainer .detailContainer {
  display: flex;
  flex-direction: column;
}
#parent_results #results div a .resultContainer .detailContainer h3,
#parent_results #results div a .resultContainer .detailContainer p {
  width: 130px;
  word-wrap: break-word;
}